@import "../../Utilities/variables";

.home__container {
    width: 100%;
    height: 100vh;
    max-height: 100vh;
    overflow: hidden;
    background-size: cover;
    background-position: center;

    .home__main {
        height: 100%;
        display: flex;
        gap: $homeGap;
        padding: 150px 80px 80px;

        .home__left-section {
            max-width: 25%;
            flex: 25%;
            height: 100%;
            // background-color: rgba(255, 255, 255, 0.5);
        }

        .home__middle-section {
            max-width: 50%;
            flex: 50%;
        }

        .home__right-section {
            max-width: 25%;
            flex: 25%;
            height: 100%;
            // background-color: rgba(255, 255, 255, 0.5);
        }
    }

    @media only screen and (max-width: $desktop) {
        .home__main {
            .home__right-section {
                display: none;
            }

            .home__left-section {
                max-width: 40%;
                flex: 40%;
            }

            .home__middle-section {
                max-width: 60%;
                flex: 60%;
            }
        }
    }

    @media only screen and (max-width: $tablet) {
        .home__main {
            padding: 150px 48px 80px;
            .home__left-section {
                display: none;
            }

            .home__middle-section {
                max-width: 100%;
                flex: 100%;
            }
        }
    }

    @media only screen and (max-width: $phone) {
        .home__main {
            padding: 150px 0px 0px;
        }
    }
}

.light.home__container {
    background-image: $lightModeBG;
}

.dark.home__container {
    background-image: $darkModeBG;
}
