@import "../../Utilities/variables";

.suggestedprofile__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-family: $sansSerifMain;
    color: $lightModeGrey;

    .suggestedprofile__details {
        display: flex;
        align-items: center;
        gap: 12px;

        .suggestedprofile__avatar {
            min-width: 50px;
            min-height: 50px;
            width: 50px;
            height: 50px;
            border-radius: 50%;
            overflow: hidden;

            img {
                min-width: 100%;
                min-height: 100%;
                max-width: 100%;
                max-height: 100%;
                object-fit: cover;
            }
        }

        .suggestedprofile__details-name {
            display: flex;
            flex-direction: column;

            h3 {
                font-size: 1em;
            }

            p {
                font-size: 0.8em;
            }
        }
    }

    .suggestedprofile__follow {
        color: $mainBlue;
        font-family: $sansSerifMain;
        font-weight: 500;
        background: none;
        border: 2px solid $mainBlue;
        border-radius: 150px;
        padding: 4px 18px;
        transition: color 0.2s ease, background 0.2s ease;

        &:hover {
            background: $mainBlue;
            color: white;
            cursor: pointer;
        }
    }

    .suggestedprofile__following {
        color: white;
        font-family: $sansSerifMain;
        font-weight: 500;
        background: $mainBlue;
        border: 2px solid $mainBlue;
        border-radius: 150px;
        padding: 4px 18px;
        transition: color 0.2s ease, background 0.2s ease, border 0.2s ease;

        &:hover {
            background: $unfollowRed;
            border: 2px solid $unfollowRed;
            color: white;
            cursor: pointer;
        }
    }
}
