.suggested__container {
  padding: 36px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  gap: 24px;
}

.suggested__container h3 {
  font-family: "Roboto", sans-serif;
}

.suggested__container .suggested__profiles {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  gap: 12px;
}

.light.suggested__container {
  background-color: rgba(255, 255, 255, 0.9);
}

.light.suggested__container h3 {
  color: #5379a6;
}

.dark.suggested__container {
  background-color: rgba(25, 40, 70, 0.621);
}

.dark.suggested__container h3 {
  color: #e0e7f0;
}
